@import '../Header.scss';
#index-header {
  display: inherit; //~~|🠈 display: grid; 🠈|~~//
  grid-area: 1 / 1 / 2 / 4; //~~|🠊 row-start / col-start / row-end / col-end 🠈|~~//
  div {
    display: flex;
    align-items: center;
    img {
      height: 50%;
    }
  }
  //--|🠋 Desktop (Landscape) - ☒ 🠋|--//
  @media screen and (orientation: landscape) and (min-aspect-ratio: 16/9) {
    //--|🠊 display: grid; 🠈|--//
    grid-template: 4rem / 3rem 2fr repeat(3, 1fr) 3rem; //~~|🠊 rows / columns 🠈|~~//
    div {
      grid-area: 1 / 2 / 2 / 3; //~~|🠊 row-start / col-start / row-end / col-end 🠈|~~//
      justify-content: flex-start;
      //--|🠊 display: flex; 🠈|--//
      img {
        padding-right: 3rem;
      }
    }
    .header-home {
      grid-area: 1 / 3 / 2 / 4; //~~|🠊 row-start / col-start / row-end / col-end 🠈|~~//
    }
    .header-skills {
      grid-area: 1 / 4 / 2 / 5; //~~|🠊 row-start / col-start / row-end / col-end 🠈|~~//
    }
    .header-contact {
      grid-area: 1 / 5 / 2 / 6; //~~|🠊 row-start / col-start / row-end / col-end 🠈|~~//
    }
    background: rgba($red-dark, 0.75);
    //~~|🠊 border: 4px dashed $red-neon; 🠈|~~//
    //~~|🠊 background: rgba($red-dark, 0.75); 🠈|~~//
  }
  //--|🠋 Mobile (Portrait) - ☒ 🠋|--//
  @media screen and (orientation: portrait) and (max-aspect-ratio: 1/1) {
    //--|🠊 display: grid; 🠈|--//
    div {
      //--|🠊 display: flex; 🠈|--//
      justify-content: center;
    }
    background: rgba($blue-dark, 0.75);
    //~~|🠊 border: 4px dashed $blue-neon; 🠈|~~//
    //~~|🠊 background: rgba($blue-dark, 0.75); 🠈|~~//
  }
  //--|🠋 Tablet (Square) - ☒ 🠋|--//
  @media screen and (max-aspect-ratio: 16/9) and (min-aspect-ratio: 1/1) {
    //--|🠊 display: grid; 🠈|--//
    grid-template: 4rem / 2rem auto 1fr 2rem; //~~|🠊 rows / columns 🠈|~~//
    div {
      //--|🠊 display: flex; 🠈|--//
      grid-area: 1 / 2 / 2 / 3; //~~|🠊 row-start / col-start / row-end / col-end 🠈|~~//
      img {
        padding-right: 2rem;
      }
    }
    .header-projects {
      grid-area: 1 / 3 / 2 / 4; //~~|🠊 row-start / col-start / row-end / col-end 🠈|~~//
      justify-content: flex-end;
    }
    background: rgba($green-dark, 0.75);
    //~~|🠊 border: 4px dashed $green-neon; 🠈|~~//
    //~~|🠊 background: rgba($green-dark, 0.75); 🠈|~~//
  }
}
